import { Component, Fragment, h, JSX, Prop } from '@stencil/core';
import { isModelFuelTypeGasoline, isString } from '../../utility/helper';

@Component({
  tag: 'phn-signature-heading',
  styleUrl: 'signature-heading.scss',
  scoped: true
})
export class SignatureHeading {
  @Prop() text!: string;
  @Prop() subtext?: string;

  render(): JSX.Element {
    return (
      <Fragment>
        <phn-p-model-signature model={this.text.toLowerCase()} size="inherit" class="model-signature" />

        {isString(this.subtext) && !isModelFuelTypeGasoline(this.text) ? (
          <phn-p-text
            color="inherit"
            class="subtext"
            weight="regular"
            size="x-small"
            tag="div"
            ellipsis="true"
            data-test-id="subtext"
          >
            {this.subtext}
          </phn-p-text>
        ) : null}
      </Fragment>
    );
  }
}
